import { Input, Component } from '@angular/core';
import { RecommendationResponse } from '../../shared/models/base.model';
import { AggregatorService } from '../../shared/services/aggregator.service';
import { KeyConst } from '../../shared';

@Component({
    selector: 'katana-recommendation',
    templateUrl: './recommendation.component.html',
    styleUrls: ['./recommendation.component.scss']
})

export class RecommendationComponent {
    @Input() public data = new RecommendationResponse({});

    constructor(public aggregatorService: AggregatorService) { }

    public close(): void {
        this.data.show = false;
        this.data.keyword = '';
    }

    public search(all: boolean = false): void {
        this.data.all = all;
        this.data.show = false;
        this.aggregatorService.publish(KeyConst.Search, this.data);
    }
}
